export default {
    namespaced: true,
    state: {
        online: [],
        list: [],
    },
    getters: {
        isOnline: (state) => (id) => {
            return !!state.online.includes(id);
        },
        newList(state){
            let ids_list = state.list.map(i => i.id);
            return state.online.filter(x => !ids_list.includes(x));
        },
        getId: (state) => (id) => {
            return state.list.find(x => x.id === id)
        }
    },
    mutations: {
        setOnline(state, data){
            state.online = data
        },
        setList(state, data){
            state.list = data
        },
    },
    actions: {
        setOnline({commit}, data){
            commit('setOnline', data)
        },
        setList({commit}, data){
            commit('setList', data)
        }
    }
}
