export default {
    namespaced: true,
    state: {
        wifiList: [],
        currentWifi: null,
        baseColor: 175.82130731969337,
        errorColor: 359.44533323151444,
        mdm: true,
        drive: [],
        network: {
            ethernet: {
                ip: '',
                port: "3000",
                mac: ''
            },
            wifi: {
                ip: '',
                port: "3000",
                mac: '',
                ssid: '',
                password: ''
            }
        }
    },
    getters: {
        drive: (state) => {
            return state.drive.map(i => {
                return {
                    id: i.mountpoints[0].path,
                    label: i.description
                }
            })
        }
    },
    mutations: {
        setWifiList(state, data){
            state.wifiList = data
        },
        setCurrentWifi(state, data){
            state.currentWifi = data;
        },
        setBaseColor(state, data){
            state.baseColor = data
        },
        setErrorColor(state, data){
            state.errorColor = data
        },
        setMdm(state, data){
            state.mdm = data
        },
        setDrive(state, data){
            state.drive = data;
        },
        setNetwork(state, data){
            state.network = data;
        }
    },
    actions: {
        setWifi({commit}, data){
            commit('setWifiList', data.list);
            commit('setCurrentWifi', data.current);
        },
        setBaseColor({commit}, data){
            commit('setBaseColor', data);
        },
        setErrorColor({commit}, data){
            commit('setErrorColor', data);
        },
        setMdm({commit}, data){
            commit('setMdm', data);
        },
        setDrive({commit}, data){
            commit('setDrive', data);
        },
        setNetwork({commit}, data){
            commit('setNetwork', data);
        },
    }

}
