export default {
    namespaced: true,
    state: {
        list: []
    },
    mutations: {
        add(state, data){
            if(state.list.length === 30){
                state.list.splice(29, 1)
            }
            state.list.unshift(data);
        }
    },
    actions: {
        add({commit}, data){
            commit('add', data)
        }
    }
}
