const IS_DEV = false;

import Vue from 'vue'
import App from './App.vue'

// Vuesax Component Framework
import Vuesax from 'vuesax'
import 'material-icons/iconfont/material-icons.css' //Material Icons
import 'vuesax/dist/vuesax.css'; // Vuesax
Vue.use(Vuesax)


// Theme Configurations
import '../themeConfig.js'


// Globally Registered Components
import './globalComponents.js'


// Styles: SCSS
import './assets/scss/main.scss'


// Tailwind
import '@/assets/css/main.css'


// Vue Router
import router from './router'


// Vuex Store
import store from './store/store'


// Vuejs - Vue wrapper for hammerjs
import { VueHammer } from 'vue2-hammer'
Vue.use(VueHammer)


// PrismJS
import 'prismjs'
import 'prismjs/themes/prism-tomorrow.css'


// Feather font icon
require('./assets/css/iconfont.css')


// Vue select css
// Note: In latest version you have to add it separately
// import 'vue-select/dist/vue-select.css';


Vue.config.productionTip = false;

const server = IS_DEV ? 'http://192.168.1.61:3000' : '';

import axios from 'axios';

const http = axios.create({
    baseURL: server + '/api/v1/',
});

Vue.prototype.$http = http;

http.get('/wifi').then((res) => {
    store.dispatch('settings/setWifi', res.data);
})

http.get('/settings').then((res) => {
    store.dispatch('settings/setBaseColor', res.data.baseColor.hue);
    store.dispatch('settings/setErrorColor', res.data.errorColor.hue);
    store.dispatch('settings/setMdm', res.data.mdm);
    store.dispatch('settings/setDrive', res.data.drive);
})

http.get('/bagholders').then((res) => {
    store.dispatch('bagholders/setList', res.data);
});

http.get('/bagholders/online').then((res) => {
    store.dispatch('bagholders/setOnline', res.data);
});

import io from 'socket.io-client';

// let bagholder1 = io('http://192.168.1.61:3000', {query: {q: 'b8:27:eb:25:d0:a1'}});
// let bagholder2 = io('http://192.168.1.61:3000', {query: {q: 'b8:27:eb:25:d0:a2'}});
// let bagholder3 = io('http://192.168.1.61:3001', {query: {q: 'b8:27:eb:25:d0:a3'}});
// let bagholder4 = io('http://192.168.1.61:3001', {query: {q: 'b8:27:eb:25:d0:a4'}});
// let bagholder5 = io('http://192.168.1.61:3001', {query: {q: 'b8:27:eb:25:d0:a5'}});
// let bagholder6 = io('http://192.168.1.61:3001', {query: {q: 'b8:27:eb:25:d0:a6'}});
// let bagholder7 = io('http://192.168.1.61:3001', {query: {q: 'b8:27:eb:25:d0:a7'}});
// let bagholder8 = io('http://192.168.1.61:3001', {query: {q: 'b8:27:eb:25:d0:a8'}});
// let bagholder9 = io('http://192.168.1.61:3001', {query: {q: 'b8:27:eb:25:d0:a9'}});
// let bagholder10 = io('http://192.168.1.61:3001', {query: {q: 'b8:27:eb:25:d0:b1'}});

const socket = io(server + '/');
Vue.prototype.$io = socket;

socket.on('disconnect', () => {
    store.dispatch('bagholders/setOnline', []);
})

socket.on('setting:wifi:scan', (data) => {
    store.dispatch('settings/setWifi', data);
});

socket.on('settings:color:set:base', (data) => {
    store.dispatch('settings/setBaseColor', data.hue);
});

socket.on('settings:color:set:error', (data) => {
    store.dispatch('settings/setErrorColor', data.hue);
});

socket.on('settings:mdm:set', (data) => {
    store.dispatch('settings/setMdm', data);
});

socket.on('log:http', (data) => {
    store.dispatch('httpLog/add', data);
});

socket.on('bagholders:online', (data) => {
    store.dispatch('bagholders/setOnline', data);
});

socket.on('bagholders:update', (data) => {
    store.dispatch('bagholders/setList', data);
});

socket.on('setting:drive:list', (data) => {
    store.dispatch('settings/setDrive', data);
});

socket.on('info:networking', (data) => {
    store.dispatch('settings/setNetwork', data);
});




new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')
